* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
body {
  font-family: "Open Sans", sans-serif;
  background: #f0f2f5;
}
.navbar {
  width: 100%;
  height: 55px;
  background: #fff;
  display: flex;
  align-items: center;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  padding: 0 30px;
  position: fixed;
  top: 0;
  z-index: 100;
}
.navbar__first {
  display: flex;
  align-items: center;
  width: 25%;
}
.navbar__first-logo {
  width: 13%;
}
.navbar__logo {
  font-size: 40px;
  color: #0d8cf1;
}
.navbar__first-search {
  width: 75%;
  position: relative;
}
.navbar__first-searchbar {
  width: 100%;
  padding: 12px 45px;
  border: none;
  background: #f0f2f5;
  border-radius: 30px;
  outline: none;
  font-size: 12px;
}
.navar__searchIcon {
  position: absolute;
  left: 20px;
  top: 13px;
  font-size: 19px;
  color: silver;
}

.navbar__middle {
  width: 50%;
  display: flex;
  padding: 0 30px;
  justify-content: center;

}
.navbar__middle-icons {
  font-size: 65px;
  color: #606770;
  cursor: pointer;
  
}
.middleIcon {
  display: inline-block;
  position: relative;
  margin-left: 10px;
  margin-right: 10px;
  padding: 5px 30px;
  border-radius: 10px;
  cursor: pointer;
}
.middleIcon:hover{
  background: #f0f2f5;
}
.navbar__notify {
  display: flex;
  position: absolute;
  width: 20px;
  height: 20px;
  background: #f1327b;
  top: 0px;
  right: 20px;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  font-size: 12px;
  color: #fff;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
}
.navbar__last {
  width: 25%;
  display: flex;
  justify-content: center;
}
.navbar__last-section {
  margin: 0 3px;
  display: flex;
  width: 45px;
  height: 45px;
  background: #f0f2f5;
  align-items: center;
  justify-content: center;
  border-radius: 50%;
  cursor: pointer;
}

.navbar__last-section:hover{
  background: #d4d5d6;
}
.img-hover{
  margin: 0 5px;
  padding-left: 5px;
  padding-right: 5px;
  display: flex;
  width: 100px;
  height: 45px;
  align-items: center;
  justify-content: center;
  border-radius: 10px;
  cursor: pointer;  

}
.img-hover img{
  border-radius: 100%;
  width: 30px;
  height: 30px;
}
.img-hover span{
  margin-left: 2px;
  color: rgb(41, 38, 38);
  font-size: 14px;
}
.img-hover:hover{
  background: #f0f2f5;
}
.facebook {
  display: flex;
  margin-top: 20px;
}
.sidebar {
  width: 25%;
  position: fixed;
  top: 90px;
  
  
}
.sidebar_content{
  width: 100%;
  overflow-y: hidden;
  height: 100vh;
}
.sidebar_content:hover{
  overflow-y: scroll;
}
#style-1::-webkit-scrollbar
{
	width: 7px;
	background-color: #bdb7b7;
  border-radius: 10px;
}

#style-1::-webkit-scrollbar-thumb
{
	border-radius: 10px;
	-webkit-box-shadow: inset 0 0 6px rgba(0,0,0,.3);
	background-color:   #bbbec4;
  
  ;
}
.sidebar__list {
  display: flex;
  padding: 6px 20px;
  align-items: center;
  cursor: pointer;
  border-radius: 10px;
}
.sidebar__list:hover{
  background: #c6c9ce;
  
}
.img-user-sidebar{
width: 10%;
}
.img-user-sidebar img{
  width: 100%;
  border-radius: 100%;
}
.sidebar__list-name {
  margin-left: 10px;
}

.posts {
  width: 50%;
  margin-left: 25%;
  margin-top: 50px;
}
.product {
  width: 100%;
}
.product__col {
  width: 20%;
float: left;
padding: 10px;
}
.stories {
  width: 100%;
  display: flex;
}
.stories__col {
  width: 20%;
padding-left: 10px;
}
.stories_user__body {
  height: 200px;
  position: relative;
  border-radius: 10px;
  overflow: hidden;
}
.stories_user__body:hover{
  background-color: rgb(0, 0, 0,0.2);
}
.stories_user__body img {
  width: 100%;
  height: 75%;
  border-top-left-radius: 10px;
  border-top-right-radius: 10px;
}
.stories_user__body-overlay {
  position: absolute;
  width: 100%;
  height: 25%;
  bottom: 0;
  left: 0;
  background-color:white;
}
.create-story{
  position: relative;
  bottom: 30px;
  left: 50px;
  background-color:white;
  padding: 10px;
  width: 35%;
  color: white;
  border-radius: 50%;
}
.create-story-icon{
  
  background-color: #0d8cf1;
  padding: 7px;
  width: 100%;
  color: white;
  border-radius: 50%;
}
.stories_user__body-name {
  position: absolute;
  bottom: 10px;
  left: 20px;
  font-size: 14px;
  margin: 10px;
}
.stories__body {
  height: 200px;
  position: relative;
  border-radius: 10px;
  overflow: hidden;
  width: 100%;
}

.stories__body img {
  width: 100%;
  height: 100%;
  border-radius: 10px;
}
.stories__body-overlay {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  background-color: rgb(0, 0, 0,0.1);
}
.stories__body-overlay:hover{
  background-color: rgb(0, 0, 0,0.2);
}
.stories__body-overlay-img {
  width: 30px;
  height: 30px;
  border-radius: 50%;
  overflow: hidden;
  margin-top: 10px;
  margin-left: 20px;
  border: 4px solid #0d8cf1;
}
.stories__body-overlay-img img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.stories__body-name {
  position: absolute;
  bottom: 10px;
  left: 10px;
  color: #fff;
  font-size: 13px;
}
.create {
  width: 80%;
  margin: 15px auto;
  padding: 15px;
  background: #fff;
  box-shadow: 0 1px 2px rgba(0, 0, 0, 0.3);
  border-radius: 10px;
}
.create__first {
  display: flex;
  width: 100%;
}
.create__first-img {
  width: 10%;
}
.create__first-img span {
  display: inline-block;
  width: 45px;
  height: 45px;
  border-radius: 50%;
  overflow: hidden;
}
.create__first-img span img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.create__first-input {
  width: 90%;
}
.create__first-inputs {
  width: 100%;
  padding: 10px 20px;
  border: none;
  background: #f0f2f5;
  border-radius: 30px;
  outline: none;
  font-size: 14px;;
}

.create__second {
  width: 100%;
  border-top: 1px solid #f0f2f5;
  margin-top: 12px;
  padding-top: 10px;
  display: flex;
  justify-content: space-between;
}
.create__second-icon {
  display: flex;
  align-items: center;
  cursor: pointer;
  padding: 10px;
  border-radius: 10px;
  width: 33%;
  display: flex;
  justify-content: center;
}
.create__second-icon:hover{
  background: #f0f2f5;
}
.text {
  margin-left: 7px;
}
.redColor {
  color: #e72173;
  font-size: 23px;
}
.greenColor {
  color: #2aec4a;
  font-size: 23px;
}
.orangeColor {
  color: #f8f525;
  font-size: 23px;
}

.show {
  width: 80%;
  margin: 15px auto;
  background: #f0f2f5;
  border-radius: 10px;
  box-shadow: 0 1px 2px rgba(0, 0, 0, 0.3);
}
.show__header {
  display: flex;
  align-items: center;
  width: 100%;
}
.show__header-img {
  width: 35px;
  height: 35px;
  border-radius: 50%;
  overflow: hidden;
}
.show__header-img img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.show__header-name {
  margin-left: 12px;
  font-weight: 600;
  justify-content: right;
}
.post-option{
  position: relative;
  top: 0px;
  right: -400px;
  width: 7%;
  border-radius: 100%;
  padding: 8px;
  font-size: 22px;
  cursor: pointer;
}
.post-option:hover{
background-color: #d5d9df;
}
.date {
  color: silver;
  font-weight: 300;
  font-size: 14px;
}
.show__body {
  width: 100%;
  display: flex;
  flex-direction: column;
}
.show__body-text {
  width: 100%;
  margin: 12px 0;
}
.show__body-img img {
  width: 100%;
  height: 100%;
}
.show__reactions {
  border-top: 1px solid #f0f2f5;
  border-bottom: 1px solid #f0f2f5;
  display: flex;
  justify-content: space-between;
  padding: 10px 0;
  cursor: pointer;
}
.reactions {
  display: flex;
  align-items: center;
}
.reactions-text {
  display: inline-block;
  margin-left: 5px;
}
.empty {
  margin: 20px 0;
  padding: 14px;
  background: #fff;
}
.chatBar {
  width: 25%;
  position: fixed;
  top: 60px;
  right: 0;
  overflow-y: scroll;
}
.card-header{
  width: 100%;
}
.card-header-img{
  float: left;
}
.card-header-name{
  float: left;
  white-space: nowrap;
}
.card-header-close{
  float: right;
}
.card-body{
  width: 100%;
}
.card-body img{
  width: 100%;
  height: 150px;
}
.card-footer{
  width: 100%;
}
.card-footer button{
width: 100%;
padding: 10px;
font-size: 17px;
border:none;
border-radius: 10px;
cursor: pointer;
}
.card-footer button:hover{
  background-color: #bbbec4;
}
.chatBar__list {
  display: flex;
  align-items: center;
  padding: 10px 25px;
  cursor: pointer;
}
.chatBar__list-img {
  width: 35px;
  height: 35px;
  position: relative;
}
.status {
  display: block;
  width: 10px;
  height: 10px;
  background: green;
  position: absolute;
  bottom: 0;
  border-radius: 50%;
  right: 0;
  border: 1.4px solid #f0f2f5;
}
.chatBar__list-img img {
  width: 100%;
  height: 100%;
  border-radius: 50%;
  object-fit: cover;
}
.chatBar__list-name {
  margin-left: 10px;
  font-weight: 600;
}
.chat {
  position: fixed;
  bottom: 0;
  right: 10%;
  height: 450px;
  min-height: 450px;
  max-height: 450px;
  background: #ffffff;
  width: 330px;
  border-top-left-radius: 7px;
  border-top-right-radius: 7px;
  overflow: hidden;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
}
.chat__header {
  display: flex;
  padding: 15px;
  border-bottom: 1px solid #f0f2f5;
}
.chat__first {
  width: 50%;
  display: flex;
  align-items: center;
}
.chat__header-img {
  width: 30px;
  height: 30px;
  border-radius: 50%;
  overflow: hidden;
}
.chat__header-img img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.chat__header-name {
  margin-left: 6px;
  font-weight: 600;
}
.chat__second {
  width: 50%;
  display: flex;
  justify-content: flex-end;
}
.chat__header-icons {
  margin: 10px 5px;
  color: silver;
  cursor: pointer;
}
.chat__body {
  padding: 14px;
  font-size: 14px;
  
}
.chat__left {
  display: flex;
  justify-content: flex-start;
  align-items: center;
}
.chat__left img{
width: 10%;
border-radius: 100%;
height: 30px;
}
.chat__left p {
  display: inline-block;
  background: #f0f2f5;
  padding: 7px;
  color: #333;
  border-radius: 6px;
  margin-top: 6px;
  margin-left: 3px;
  font-size: 14px;
}
.chat__right {
  display: flex;
  justify-content: flex-end;
  margin-top:5px;
  align-items: center;
}
.chat__right img{
width: 10%;
border-radius: 100%;
height: 30px;
margin-left: 3px;
}
.chat__right p {
  display: inline-block;
  background: #0d8cf1;
  padding: 7px;
  color: #fff;
  border-radius: 6px;
  margin-top: 6px;
  margin-left: 3px;
}
.chat__footer {
  position: fixed;
  bottom: 0px;
  width: 400px;
}
.chat__input {
  width: 330px;
  border: none;
  background: #f0f2f5;
  outline: none;
  padding: 15px 13px;
}
